.home {
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: start;
  position: relative;
  flex-direction: column;

  background: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)),
    url("/public/asstes/bgimg1.jfif");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.home .textContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-left: 30%;
}

.home .textContent .textComponent h2 {
  font-size: 40px;
  color: #ffffff;
  font-family: "Poppins";
}

.home .textContent .textComponent strong {
  font-size: 40px;
  color: #efba00;
  font-family: "Poppins";
}

.home .textContent .slide {
  padding-top: 10%;
}
