.fourthPage {
  padding: 0;
  margin: 0;
  height: 100vh;
  text-align: center;
}

.fourthPage h2 {
  padding-top: 20px;
  font-size: 28px;
  position: relative;
  z-index: 999;
}

.fourthPage h2 strong {
  font-size: 28px;
}

.yourGateway {
  color: #777;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.featuresContainer {
  display: flex;
  justify-content: space-evenly;
}

.featuresContainer .container {
  margin: 30px;
}

.featuresContainer .container img {
  width: 250px;
  height: 250px;
}

.featuresContainer .container h1 {
  color: #000;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.featuresContainer .container p {
  color: #000;
  text-align: center;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.fourthPage button {
  margin-left: 500px;
}
