.chemistryKit {
  border-radius: 30px;
  background: #fff;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 15px 0;
  margin-left: 30px;
  width: 200px;
  height: 280px;
  font-family: "Poppins";
}

.chemistryKit .chemistryKitContainer {
  width: 90%;
  margin: 15px;
  box-sizing: border-box;
  text-align: center;
  border-radius: 20px;
  background: rgba(240, 240, 240, 0.87);
  cursor: pointer;
  font-family: "Poppins";
}

.chemistryKit .chemistryKitContainer img {
  width: 100px;
  height: 100px;
  text-align: center;
  margin: 0 auto;
  display: block;
}

.chemistryKit .chemistryKitContainer button {
  text-align: center;
  color: wheat;
  width: 100%;
  padding: 15px;
  border: 0;
  outline: none;
  cursor: pointer;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;

  font-family: "Poppins";
  border-radius: 0px 0px 21px 21px;
  background: #e43700;

  color: #fff;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.chemistryKit .webbyDiy {
  color: #000;
  font-family: "Poppins";
  font-size: 12px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;
}

.chemistryKit .offerPrice {
  color: #000;
  font-family: "Poppins";
  font-size: 12px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  padding: 10px;
}

.chemistryKit .originalPrice {
  margin-left: 12px;
  text-decoration: line-through;
  color: #e43700;
}

.chemistryKit .msg {
  color: #777;
  font-family: "Poppins";
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}
