.btnPrimary button {
  color: #fff;
  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;

  padding: 20px;
  border-radius: 10px;
  background: #efba00;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  border-style: none;
  cursor: pointer;
}

.btnPrimary img {
  width: 30px;
  margin-left: 20px;
  margin-top: 5px;
}
