.thirdPage {
  margin-top: 0px;
  padding-top: 0;
  background: #063042;
  height: 130vh;
}
.thirdPageContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.thirdPageContainer h2:first-child {
  padding-top: 20px;
  font-size: 24px;
  color: #efba00;
  position: relative;
  z-index: 999;
  font-family: "Poppins";
}

.thirdPageContainer h2 strong {
  font-size: 24px;
  font-family: "Poppins";
}

.theme {
  text-align: center;
  color: #ffffff;
  font-family: "Poppins";
  font-size: 20px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
  position: relative;
  z-index: 999;
  padding-bottom: 40px;
}

.backgroundIcons {
  position: absolute;
  z-index: 12;
}

.backgroundIcons .liquid-blobby1 {
  width: 100px;
  height: 100px;
  position: absolute;
  filter: drop-shadow(1px 1px 1px #777);
  margin-left: 700px;
  margin-top: 50px;
}
.backgroundIcons .liquid-blobby2 {
  width: 100px;
  height: 100px;
  position: absolute;
  filter: drop-shadow(1px 1px 1px #777);
}

.backgroundIcons .liquid-blob1 {
  position: absolute;
  width: 400px;
  height: 300px;
  filter: drop-shadow(1px 1px 1px #777);
  margin-left: 200px;
  margin-top: 200px;
}
.backgroundIcons .liquid-blob2 {
  position: absolute;
  width: 300px;
  height: 200px;
  filter: drop-shadow(2px 2px 2px #777);
  margin-left: 1000px;
  margin-top: 300px;
}

.thirdPage .discover {
  position: absolute;
  color: #efba00;
  font-family: "Poppins";
  font-size: 20px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  margin-top: 890px;
  padding-top: 60px;
}

.plans {
  position: absolute;
  margin-top: 1050px;
  display: flex;
  padding-top: 130px;
}
