.header {
  height: 100px;
  background: #063042;
  z-index: 11;
  color: white;
  font-size: 28px;
  display: flex;
  justify-content: space-between;
}

.header p {
  color: yellow;
  margin-top: 30px;
  margin-left: 20px;
  font-size: 30px;
}

.header ul {
  margin-top: 20px;
  display: flex;
}

.header li {
  list-style: none;
  padding: 20px;
  cursor: pointer;
}

.header li:last-child {
  margin-left: 200px;
}
