.secondPage {
  margin: 0px;
  padding: 0;
  height: 100vh;
  background: #f8f5ec;
  padding-top: 70px;
}
.secondPageContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  z-index: 2;
}

.secondPageContainer h2 {
  font-family: "Poppins";
  padding-top: 20px;
  font-size: 28px;
  color: black;
}

.secondPageContainer h2 strong {
  font-family: "Poppins";
  font-size: 28px;
  color: black;
}

.discover {
  text-align: center;
  color: #777;
  font-family: "Poppins";
  font-size: 18px;
  font-style: normal;
  font-weight: 300;
  line-height: normal;
}

.chemistryContainer {
  display: flex;
  margin-left: 100px;
}

.backgroundIcons {
  position: absolute;
  z-index: 1;
}

.backgroundIcons .atom1 {
  width: 100px;
  height: 100px;
  position: relative;
  margin-left: 100%;
  filter: drop-shadow(1px 1px 1px #777);
}
.backgroundIcons .atom2 {
  width: 100px;
  height: 100px;
  position: relative;
  margin-top: 400px;
  filter: drop-shadow(1px 1px 1px #777);
}

.backgroundIcons .bubble1 {
  position: relative;
  margin-left: 90px;
  width: 200px;
  height: 200px;
  filter: drop-shadow(1px 1px 1px #777);
}
.backgroundIcons .bubble2 {
  position: relative;
  margin-left: 90px;
  width: 200px;
  height: 200px;
  filter: drop-shadow(2px 2px 2px #777);
}
.backgroundIcons .bubble3 {
  position: relative;
  margin-left: 90px;
  width: 200px;
  height: 200px;
  filter: drop-shadow(1px 1px 1px #777);
}
