.kitsContainer {
  z-index: 95;
  position: absolute;
  margin-top: 470px;
  display: flex;
  flex-direction: row;
}

.kitsContent {
  width: 300px;
  height: 350px;
  margin: 20px;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.kitsContent h1 {
  padding-top: 10px;
  color: #fff;
  font-family: Poppins;
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
}

.kitsContent img {
  padding-top: 10px;
  width: 150px;
  height: 150px;
}

.kitsContent p {
  padding-top: 10px;
  color: #fff;
  text-align: center;
  font-family: Poppins;
  font-size: 20px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.kitsContent button {
  color: #000;
  font-family: Nunito;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 5px 50px 5px 50px;
  border-style: none;
  border-radius: 7px;
  background: linear-gradient(0deg, #fff 0%, #fff 100%),
    linear-gradient(0deg, #fff 0%, #fff 100%),
    linear-gradient(0deg, #fff 0%, #fff 100%), #fff;
  cursor: pointer;
}
